.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.movieList{
  list-style: none;
  margin: 0;
  padding: 20px; 
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px; 
}
.movieList img {
  width: 75%;
  box-shadow:  4px 3px 3px rgba(0, 0, 0, 0.2)
}
input {
  border-radius: 5px;
  outline: none;
  font-size: 16px; 
  box-shadow: none;
  border:none;
  line-height: 24px;
  width: 50%;
  background-color: white;
  margin: 10px;
}